	module.exports = {"data":{"markdownRemark":{"html":"<h2>Sketch</h2>\n<p>Sketch is a professional design application for the Mac that utilizes a vector-based workflow. The interface is clean and inviting for new users as well as being a familiar tool for users of Adobe Fireworks. Sketch has become a popular alternative tool to Photoshop for users who do not need an over abundance of options for interface designs.</p>\n<h3>Why Sketch?</h3>\n<ul>\n<li>Easily reuse elements</li>\n<li>Prototyping</li>\n<li>Collaborate with others</li>\n<li>Lots of plugins</li>\n<li>Export files in multiple formats</li>\n<li>Vast community</li>\n<li>Exports style guide in easy to read HTML/CSS </li>\n</ul>\n<h3>Why Should I Switch To Sketch?</h3>\n<p>Sketch is designed to build digital products rather than editing photos and print graphics. Sketch’s clean interface helps keep you organized when mocking up or building out your UI/UX designs. The art boards that Sketch uses enforces a pixel grid for precise designs. Exporting designs is simple and supports different device resolutions.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>The Sketch Website - <a href='https://www.sketchapp.com' target='_blank' rel='nofollow'>Sketch</a></p>\n<p>Sketch Documentation - <a href='https://www.sketchapp.com/docs/' target='_blank' rel='nofollow'>Sketch Documentation</a></p>\n<p>A collection of Sketch tutorials - <a href='http://learnsketch.com/tutorials' target='_blank' rel='nofollow'>Learn Sketch</a></p>\n<p>Sketch App Resources - <a href='https://www.sketchappsources.com/' target=\"_blank\" rel=\"nofollow\">https://www.sketchappsources.com/</a></p>","fields":{"slug":"/designer-tools/sketch/"},"frontmatter":{"title":"Sketch"}}},"pathContext":{"slug":"/designer-tools/sketch/"}}

